.Posts {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  width: 80%;
  margin: auto;
  margin-bottom: 15rem;
}
.SeeMoreBtn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}
.SeeMore {
  margin: 16px auto 100px;
  border: none;
  border-radius: 3px;
  background-color: #264653;
  padding: 10px 24px;
  color: #f4a261;
  cursor: pointer;
  -webkit-transition: padding 300ms;
  -o-transition: padding 300ms;
  transition: padding 300ms;
}

.SeeMore:hover {
  color: white;
  background-color: #f4a261;
}

.Limit {
  text-align: center;
  color: white;
  background-color: #264653;
  padding: 10px 24px;
  border-radius: 3px
}


